<template>
  <div id="app">
    <el-container>
      <el-header>
        <el-row>
          <el-col :span="14">
            <div style="text-align: left;">
              <span style="font-size:large;">Easy Pay</span>
              <span style="font-size:small;margin-left:3px;">{{$t('EOS CPU 代付')}}</span>
            </div>
          </el-col>
          <el-col :span="3">
            <div class="lang_box">
              <div class="lang_header" @click="clickFlag">
                <img
                  :src="'static/img/' + $i18n.locale + '.png'"
                  style="height: 16px;"
                  :alt="$i18n.locale"
                />
              </div>
            </div>
          </el-col>
          <el-col :span="userAuth.name?6:7">
            <div class="right">
              <div id="login">
                <div v-if="userAuth.name">{{userAuth.name}}</div>
                <div v-else>
                  <el-button round @click="login">{{$t('登陆')}}</el-button>
                </div>
              </div>
            </div>
          </el-col>
          <el-col :span="userAuth.name?1:0">
            <div>
              <div id="logout">
                <div v-if="userAuth.name">
                  <i class="el-icon-delete" @click="logout"></i>
                </div>
              </div>
            </div>
          </el-col>
        </el-row>
      </el-header>
      <el-main>
        <div class="text">
          <h1>{{$t('没有CPU无法交易怎么办？')}}</h1>
          <p>{{$t('在这里，你可以使用EOS来支付交易所需要的CPU')}}</p>
        </div>

        <div v-loading="loading">
          <el-tabs type="border-card" stretch v-model="func" @tab-click="changeFunc">
            <!-- 转账功能 -->
            <el-tab-pane :label="$t('转账')" name="transfer">
              <div class="transfer">
                <!-- <div>
                  <el-alert center :closable="false" type="warning">{{$t('转账到交易所等公共地址请先小额尝试')}}</el-alert>
                </div>-->
                <div style="margin-top: 15px;">
                  <el-input size="small" :placeholder="$t('EOS账号')" v-model="to" @blur="checkUser">
                    <template slot="prepend">{{$t('接收方')}}</template>
                  </el-input>
                </div>
                <div style="margin-top: 15px;">
                  <el-row>
                    <el-col :span="16">
                      <el-input size="small" placeholder="0" v-model="amount" type="number">
                        <template slot="prepend">{{$t('金额')}}</template>
                      </el-input>
                    </el-col>
                    <el-col :span="8">
                      <el-select
                        v-model="curToken.symbol"
                        size="small"
                        filterable
                        @change="changeToken"
                      >
                        <el-option-group
                          v-for="group in tokenOptions"
                          :key="group.label"
                          :label="$t(group.label)"
                        >
                          <el-option
                            v-for="item in group.options"
                            :key="item.key"
                            :label="item.symbol"
                            :value="item"
                          >{{item.symbol}} @{{item.contract}}</el-option>
                        </el-option-group>
                        <!-- <el-option v-if="!tokenOptions" label="EOS" value="EOS">EOS</el-option> -->
                        <!-- <el-option
                          v-for="item in tokenOptions"
                          :key="item.key"
                          :label="item.symbol"
                          :value="item"
                        >{{item.symbol}} @{{item.contract}}</el-option>-->
                      </el-select>
                    </el-col>
                  </el-row>
                </div>
                <div v-if="curToken.balance>0" class="right" style="margin-top: 5px;height:20px;">
                  <img :src="curToken.logo" style="height:80%;" />
                  | {{$t('余额')}}:
                  <strong>{{curToken.balance}} {{curToken.symbol}}</strong>
                  @{{curToken.contract}}
                </div>
                <div style="margin-top: 15px;">
                  <el-input size="small" placeholder="memo" v-model="memo">
                    <template slot="prepend">{{$t('备注(选填)')}}</template>
                  </el-input>
                </div>
              </div>
            </el-tab-pane>
            <!-- 合约功能 -->
            <el-tab-pane :label="$t('合约')" name="contract">
              <div class="contract">
                <div>
                  <el-input size="small" :placeholder="$t('合约账号')" v-model="contract.account">
                    <template slot="prepend">{{$t('合约')}}</template>
                    <template slot="append">
                      <el-button icon="el-icon-search" @click="getAbi"></el-button>
                    </template>
                  </el-input>
                </div>
                <div v-if="contract.actionMap">
                  <el-divider>{{$t('选择操作')}}</el-divider>
                  <el-radio-group v-model="contract.action" size="mini" @change="selectAction">
                    <span v-for="action in contract.actionList" :key="action">
                      <el-radio-button style="margin:1px;" :label="action">{{ action }}</el-radio-button>
                    </span>
                  </el-radio-group>
                </div>
                <div v-if="contract.action">
                  <el-divider>{{$t('输入参数')}}</el-divider>
                  <ul>
                    <li v-for="field in contract.fields" v-bind:key="field.name">
                      <div style="margin:4px;">
                        <el-input
                          v-if="field.type=='asset'"
                          size="mini"
                          placeholder="0.0000 EOS"
                          v-model="contract.data[field.name]"
                        >
                          <template slot="prepend">{{field.name}}</template>
                          <template slot="append">{{field.type}}</template>
                        </el-input>
                        <el-input v-else size="mini" v-model="contract.data[field.name]">
                          <template slot="prepend">{{field.name}}</template>
                          <template slot="append">{{field.type}}</template>
                        </el-input>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </el-tab-pane>
            <!-- 常用功能 -->
            <el-tab-pane :label="$t('常用')" name="common">
              <div>
                <el-tabs v-model="sub_func">
                  <!-- 糖果 -->
                  <el-tab-pane :label="$t('糖果')" name="airgrab">
                    <div class="airgrab">
                      <div>
                        <el-alert center :closable="false" type="info">{{$t('airgrab_info')}}</el-alert>
                      </div>
                      <el-divider>{{$t('选择糖果')}}</el-divider>
                      <div>
                        <el-select v-model="candy" filterable @change="changeCandy">
                          <el-option-group
                            v-for="group in candies"
                            :key="group.label"
                            :label="$t(group.label)"
                          >
                            <el-option
                              v-for="item in group.options"
                              :key="item.key"
                              :label="item.symbol"
                              :value="item"
                            >{{item.symbol}} @{{item.contract}}</el-option>
                          </el-option-group>
                        </el-select>
                      </div>
                      <el-divider>{{$t('糖果介绍')}}</el-divider>
                      <div>{{$t(candy.description)}}</div>
                      <el-divider>{{$t('官方网站')}}</el-divider>
                      <div>
                        <a :href="candy.website">{{candy.website}}</a>
                      </div>
                    </div>
                  </el-tab-pane>
                  <!-- CPU/NET -->
                  <el-tab-pane :label="$t('CPU/NET')" name="cpunet">
                    <el-tabs type="border-card" stretch v-model="stake_func" @tab-click="clickCPU">
                      <el-tab-pane :label="$t('抵押')" name="stake">
                        <div style="margin-top: 15px;">
                          <el-input
                            size="small"
                            :placeholder="$t('EOS账号')"
                            v-model="stake_data.receiver"
                          >
                            <template slot="prepend">{{$t('受益人')}}</template>
                          </el-input>
                        </div>
                        <div style="margin-top: 15px;">
                          <el-input
                            size="small"
                            placeholder="0"
                            v-model="stake_data.cpu_amount"
                            type="number"
                          >
                            <template slot="prepend">{{$t('抵押')}} CPU</template>
                            <template slot="append">EOS</template>
                          </el-input>
                        </div>
                        <div style="margin-top: 15px;">
                          <el-input
                            size="small"
                            placeholder="0"
                            v-model="stake_data.net_amount"
                            type="number"
                          >
                            <template slot="prepend">{{$t('抵押')}} NET</template>
                            <template slot="append">EOS</template>
                          </el-input>
                        </div>
                        <div style="margin-top: 15px;" v-if="stake_data.receiver!=userAuth.name">
                          <el-radio v-model="stake_data.transfer" label="0">{{$t('租借')}}</el-radio>
                          <el-radio v-model="stake_data.transfer" label="1">{{$t('赠送')}}</el-radio>
                        </div>
                        <div style="margin-top: 15px;" v-if="stake_data.transfer=='1'">
                          <el-alert type="warning" :closable="false">{{$t('抵押的EOS所属权将会转移给受益人')}}</el-alert>
                        </div>
                      </el-tab-pane>
                      <el-tab-pane :label="$t('解除抵押')" name="unstake">
                        <div style="margin-top: 15px;">
                          <el-row>
                            <el-col :span="20">
                              <el-input
                                size="small"
                                :placeholder="$t('请选择')"
                                v-model="stake_data.receiver"
                              >
                                <template slot="prepend">{{$t('解押自')}}</template>
                              </el-input>
                            </el-col>
                            <el-col :span="4">
                              <el-select size="small" @change="changeUnstake">
                                <el-option
                                  v-for="item in unstake_options"
                                  :key="item"
                                  :label="$t('请选择')"
                                  :value="item"
                                >{{item.receiver}}(CPU:{{item.cpu_amount}} EOS, NET:{{item.net_amount}} EOS)</el-option>
                              </el-select>
                            </el-col>
                          </el-row>
                        </div>
                        <div style="margin-top: 15px;">
                          <el-input
                            size="small"
                            placeholder="0"
                            v-model="stake_data.cpu_amount"
                            type="number"
                          >
                            <template slot="prepend">{{$t('解押')}} CPU</template>
                            <template slot="append">EOS</template>
                          </el-input>
                        </div>
                        <div style="margin-top: 15px;">
                          <el-input
                            size="small"
                            placeholder="0"
                            v-model="stake_data.net_amount"
                            type="number"
                          >
                            <template slot="prepend">{{$t('解押')}} NET</template>
                            <template slot="append">EOS</template>
                          </el-input>
                        </div>
                      </el-tab-pane>
                      <el-tab-pane :label="$t('赎回')" name="refund">
                        <div>{{$t('refund_info')}}</div>
                        <div style="margin-top: 10px;">
                          <el-button type="primary" @click="send">{{$t('赎回')}}</el-button>
                        </div>
                      </el-tab-pane>
                    </el-tabs>
                  </el-tab-pane>
                </el-tabs>
              </div>
            </el-tab-pane>
          </el-tabs>
          <el-divider>{{$t('手续费')}}</el-divider>
          <el-radio-group v-model="fee" size="mini" @change="changeFee">
            <el-radio-button border label="1">{{$t('简单交易')}} 0.01 EOS</el-radio-button>
            <el-radio-button border label="2">{{$t('复杂交易')}} 0.05 EOS</el-radio-button>
          </el-radio-group>
          <div style="margin-top: 5px;" v-if="eosBalance">
            {{$t('余额')}}:
            <strong>{{eosBalance}} EOS</strong>
          </div>
          <div v-if="txid">
            <el-divider>{{$t('交易ID')}}</el-divider>
            <el-link type="success" :href="browserUrl" target="_blank">{{txid.substr(0,20)}}...</el-link>
          </div>
          <div v-if="errorMsg">
            <el-divider>{{$t('错误信息')}}</el-divider>
            <div class="flexx">
              <el-alert center :closable="false" type="error">{{errorMsg}}</el-alert>
            </div>
          </div>
          <!-- <el-divider></el-divider> -->
          <div style="margin-top: 30px;">
            <el-button type="primary" @click="send">{{$t('发送交易')}}</el-button>
          </div>
        </div>
      </el-main>
      <el-footer height="40px">
        {{$t('本应用由')}}
        <span class="lichang">
          <a href="http://lichang.io">{{$t('力场 lichang.io')}}</a>
        </span>
        {{$t('孵化')}} |
        <span class="mail">
          <a href="mailto:x@qsx.io">{{$t('问题反馈')}}</a>
        </span>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "App",
  created() {
    // get lang from localStorage
    if (localStorage.getItem("language")) {
      this.$i18n.locale = localStorage.getItem("language");
    } else {
      localStorage.setItem("language", this.$i18n.locale);
    }
    for (const i in this.lang.all) {
      if (this.$i18n.locale == this.lang.all[i]) {
        this.lang.now = Number(i);
      }
    }
    // auto login
    setTimeout(this.login, 2000);
  },
  data() {
    return {
      lang: {
        now: 0,
        all: ["zh", "en"]
      },
      userAuth: { name: null },
      loading: false,
      func: "transfer",
      sub_func: "airgrab",
      stake_func: "stake",
      eosBalance: null,
      curToken: {
        symbol: "EOS",
        balance: null,
        contract: "eosio.token",
        precision: 4,
        logo: "https://bloks.io/img/chains/eos.png"
      },
      to: "",
      amount: 0,
      memo: "",
      stake_data: {
        receiver: "",
        cpu_amount: 0,
        net_amount: 0,
        transfer: "0"
      },
      unstake_options: [],
      txid: null,
      errorMsg: null,
      browserUrl: "https://bloks.io",
      fee: "1",
      contract: {
        account: "",
        abi: {},
        actionList: [],
        actionMap: null,
        action: "",
        data: {},
        fields: []
      },
      tokenList: [],
      tokenOptions: [],
      numberTyeps: ["uint8", "bool", "uint16", "uint32", "uint64"],
      candies: [
        {
          label: "热门",
          options: [
            {
              symbol: "BBT",
              contract: "blockbasetkn",
              description: "BBT",
              website: "https://www.blockbase.network/"
            }
          ]
        },
        {
          label: "所有",
          options: []
        }
      ],
      candy: {
        symbol: "BBT",
        contract: "blockbasetkn",
        description: "BBT",
        website: "https://www.blockbase.network/"
      }
    };
  },
  methods: {
    login() {
      this.chainApi
        .connectIdentity()
        .then(res => {
          this.userAuth = res;
          this.getBalance();
          this.getTokenList();
          this.stake_data.receiver = res.name;
        })
        .catch(err => {
          this.$message.error(this.$t(err.toString()));
        });
    },
    logout() {
      this.curToken = {
        symbol: "EOS",
        balance: null,
        contract: "eosio.token",
        precision: 4
      };
      this.eosBalance = null;
      this.chainApi.changeIdentity().then(() => {
        this.userAuth = { name: null };
      });
    },
    clickFlag() {
      // this.lang.show = !this.lang.show;
      this.lang.now += 1;
      if (this.lang.now >= this.lang.all.length) {
        this.lang.now = 0;
      }
      this.$i18n.locale = this.lang.all[this.lang.now];
      localStorage.setItem("language", this.$i18n.locale);
      // console.log(this.lang.show);
    },
    changLang(item) {
      // console.log(item);
      this.$i18n.locale = item;
      localStorage.setItem("language", item);
      this.lang.show = !this.lang.show;
    },
    async checkUser() {
      if (this.userAuth && this.to == this.userAuth.name) {
        this.$message.error(this.$t("不能转账给自己"));
        return;
      }
      if (this.to && this.to != "") {
        try {
          await this.chainApi.checkAccountExist(this.to);
        } catch (err) {
          this.$message.error(this.$t(err.toString()));
        }
      }
    },
    async getBalance() {
      this.loading = true;
      const account = this.userAuth.name;
      const { contract, symbol } = this.curToken;
      try {
        const bal = await this.chainApi.getBalance(contract, account, symbol);
        this.curToken.balance = bal;
        if (contract == "eosio.token" && symbol == "EOS") {
          this.eosBalance = bal;
        }
      } catch (err) {
        console.error(err);
        this.$message.error(this.$t("获取余额失败"));
      } finally {
        this.loading = false;
      }
    },
    async getEosBalance() {
      const account = this.userAuth.name;
      const contract = "eosio.token",
        symbol = "EOS";
      try {
        const bal = await this.chainApi.getBalance(contract, account, symbol);
        this.eosBalance = bal;
      } catch (err) {
        console.error(err);
        this.$message.error(this.$t("获取余额失败"));
      }
    },
    async getTokenList() {
      this.tokenList = [];
      const account = this.userAuth.name;
      this.tokenList = await this.chainApi.getTokenList(account);
      // 排序
      const hot = [];
      hot.push({
        key: "eosio.token-eos-eos",
        symbol: "EOS",
        balance: 0,
        contract: "eosio.token",
        precision: 4,
        logo: "https://bloks.io/img/chains/eos.png"
      });
      const tokens = this.tokenList.concat();
      for (const token of tokens) {
        if (
          token.symbol == "LC" &&
          token.contract == "lichangtoken" &&
          token.balance > 0
        ) {
          hot.push(token);
        }
      }
      tokens.sort((a, b) => {
        return b.usd_value - a.usd_value;
      });
      this.tokenOptions = [
        {
          label: "常用",
          options: hot
        },
        {
          label: "代币",
          options: tokens
        }
      ];
    },
    changeToken(token) {
      // console.log(token);
      const { symbol, balance, contract, precision, logo } = token;
      this.curToken = { symbol, balance, contract, precision, logo };
      this.getBalance();
    },
    changeFee(mode) {
      this.fee = mode;
    },
    changeFunc() {},
    changeUnstake(item) {
      this.stake_data = item;
    },
    async clickCPU(tab, event) {
      this.stake_data = {};
      if (tab.name == "unstake") {
        this.unstake_options = [];
        const res = await this.getUnstakeOptions();
        for (const row of res.rows) {
          this.unstake_options.push({
            receiver: row.to,
            cpu_amount: Number(row.cpu_weight.split(" ")[0]),
            net_amount: Number(row.net_weight.split(" ")[0])
          });
        }
        // console.log(this.unstake_options);
      } else if (tab.name == "stake") {
        this.stake_data.receiver = this.userAuth.name;
      }
    },
    getUnstakeOptions() {
      if (!this.userAuth.name) {
        this.$message.error(this.$t("请登陆"));
        return;
      }
      return this.chainApi.getUnstakeOptions(this.userAuth.name);
    },
    async send() {
      if (!this.userAuth.name) {
        this.$message.error(this.$t("请登陆"));
        return;
      }
      this.txid = null;
      this.errorMsg = null;
      this.loading = true;
      let txs;
      try {
        switch (this.func) {
          case "transfer":
            txs = await this.chainApi.transfer(
              this.curToken.contract,
              this.userAuth,
              this.to,
              Number(this.amount),
              this.curToken.precision,
              this.curToken.symbol,
              this.memo,
              this.fee
            );
            break;
          case "contract":
            if (!this.contract.actionMap) {
              throw "请输入合约";
            }
            const params = this.contract.actionMap.get(this.contract.action);
            for (const param of params) {
              if (this.numberTyeps.includes(param.type)) {
                this.contract.data[param.name] = this.contract.data[param.name]
                  ? Number(this.contract.data[param.name])
                  : 0;
              } else {
                this.contract.data[param.name] = this.contract.data[param.name]
                  ? this.contract.data[param.name]
                  : "";
              }
            }
            txs = await this.chainApi.contract(
              this.userAuth,
              this.contract.account,
              this.contract.action,
              this.contract.data,
              this.fee
            );
            break;
          case "common":
            switch (this.sub_func) {
              case "airgrab":
                txs = await this.chainApi.airgrab(
                  this.userAuth,
                  this.candy.symbol,
                  this.fee
                );
                break;
              case "cpunet":
                this.stake_data.cpu_amount = Number(this.stake_data.cpu_amount);
                this.stake_data.net_amount = Number(this.stake_data.net_amount);
                txs = await this.chainApi.stake(
                  this.userAuth,
                  this.stake_func,
                  this.stake_data,
                  this.fee
                );
                break;
              default:
                throw "wrong action";
            }
            break;
          default:
            throw "wrong action";
        }
        let { originTx, feeTx } = txs;

        // sign originTx
        const signedOriginTx = await this.signTx(originTx, "交易确认");
        // sign feeTx
        const signedFeeTx = await this.signTx(feeTx, "支付手续费");

        const res = await this.chainApi.exec(signedOriginTx, signedFeeTx);
        this.txid = res.transaction_id;
        this.browserUrl = `https://bloks.io/transaction/${this.txid}`;
        this.$message.success(this.$t("交易成功"));
        this.loading = false;
        setTimeout(this.getEosBalance, 2000);
      } catch (err) {
        console.error(err);
        debugger;
        let errMsg;
        if (err.alert) {
          errMsg = err.alert;
          this.errorMsg = err.error;
        } else {
          errMsg = err.toString();
        }
        if (errMsg.match("交易可能失败")) {
          this.$message.error({
            message: this.$t(errMsg),
            duration: 0,
            showClose: true
          });
        } else {
          this.$message.error(this.$t(errMsg));
        }
        this.loading = false;
      }
    },
    async getAbi() {
      this.loading = true;
      try {
        this.contract.data = {};
        const abi = await this.chainApi.getAbi(this.contract.account);
        this.contract.abi = abi;
        const { structs, actions } = abi;
        const structsMap = new Map();
        for (const struct of structs) {
          structsMap.set(struct.name, struct.fields);
        }
        this.contract.actionList = [];
        const actionMap = new Map();
        for (const action of actions) {
          const fields = structsMap.get(action.type);
          actionMap.set(action.name, fields);
          this.contract.actionList.push(action.name);
        }
        this.contract.actionMap = actionMap;
        this.loading = false;
      } catch (err) {
        this.$message.error(this.$t(err.toString()));
        this.loading = false;
      }
    },
    async selectAction() {
      this.contract.fields = this.contract.actionMap.get(this.contract.action);
      console.log(this.contract.fields);
    },
    async signTx(tx, message) {
      const msg = this.$message.info({
        message: this.$t(message),
        duration: 0,
        center: true
      });
      let signedTx;
      try {
        if (!!window.imToken) {
          signedTx = await this.chainApi.signAndComfirm(tx, this.userAuth);
        } else {
          signedTx = await this.chainApi.sign(tx);
        }
      } catch (err) {
        throw "拒绝交易";
      } finally {
        msg.close();
      }

      const txSigs = signedTx.transaction.signatures;
      // console.log(txSigs);
      if (txSigs.length <= 1) {
        throw "拒绝交易";
      } else if (txSigs[1].toString().substr(0, 3) != "SIG") {
        throw "拒绝交易";
      } else {
        return signedTx;
      }
    }
  }
};
</script>

